.wrapper-side {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  .header-Details {
    width: 100%;
    flex: 0 0 50px;
    padding: 0px 16px;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
    border-bottom: 1px solid rgba(27, 31, 41, 0.2);
    .box-back {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      cursor: pointer;
      .qqq {
        position: relative;
        font-size: 16px;
        color: rgb(143, 148, 162);
        .point {
          position: absolute;
          right: -3px;
          bottom: -3px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background-color: red;
        }
      }

      span {
        margin: 0px;
        color: rgb(79, 84, 97);
        font-family: AvertaStd-Regular, Arial, "Noto Sans JP", Prompt,
          sans-serif;
        font-size: 1.6rem;
        font-weight: 400;
        line-height: 1.6;
      }
      i {
        font-size: 14px;
      }
    }
    .edit {
      padding: 10px;
      border-radius: 8px;
      transition: background-color 0.1s ease 0s;
      cursor: pointer;
      font-size: 16px;
      &:hover {
        background-color: rgba(105, 110, 124, 0.12);
      }
    }
  }

  .AccordionBrand {
    .accordion-body {
      padding: 1rem 1rem;
      margin-top: -10px;
    }
    .switch-show-name {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 10px;
      margin-top: 15px;
      .title {
        margin: 0px;
        color: rgb(79, 84, 97);
        font-family: AvertaStd-Regular, Arial, "Noto Sans JP", Prompt,
          sans-serif;
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 1.4;
      }
    }
    .accordion-item {
      border: none !important;
      margin-bottom: 20px !important;
    }
    .accordion-button::after {
      background-size: 1.7rem;
      margin-bottom: 1px;
      width: 21px;
      height: 21px;
    }
    .accordion-button {
      padding: 1rem 1.25rem;
      margin: 0px;
      font-family: AvertaStd-Regular, Arial, "Noto Sans JP", Prompt, sans-serif;
      font-size: 1.1rem;
      font-weight: 600;
      line-height: 1.4;
      color: rgb(27, 31, 41) !important;
    }
    .text-acco {
      margin: 0px;
      color: rgb(27, 31, 41);
      font-family: "Noto Sans JP", Prompt, sans-serif;
      font-size: 1.7rem;
      font-weight: 600;
      line-height: 1.4;
    }
    .accordion-button:not(.collapsed) {
      background: none !important;
      border: none !important;
      box-shadow: none !important;
    }
    .accordion-button:focus {
      background: none !important;
      border: none !important;
      box-shadow: none !important;
    }
    .question {
      color: rgb(0, 75, 196);
      cursor: pointer;
      font-size: 16px;
    }

    .box-color {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: fit-content;
      gap: 10px;
      .input-color {
        width: 93px;
        height: 43px;
      }
      .chose-color {
        border-color: #fff !important;
        cursor: pointer;
        width: 43px;
        height: 43px;
        border-radius: 8px;
      }
    }

    .box-them {
      // margin: 0px -6px;
      margin: 0;
      display: flex;
      flex-wrap: wrap;
      gap: 4px;
      .container-them {
        cursor: pointer;
        width: 76px;
        height: 76px;
        margin: 0px 6px 12px;
        border-radius: 4px;
        position: relative;
        background-color: rgb(241, 242, 245);
        transition: all 0.2s ease-in-out;
        .layer {
          width: calc(100% + 4px);
          transition: all 0.2s ease-in-out;
          height: calc(100% + 4px);
          position: absolute;
          top: -2px;
          left: -2px;
          border-radius: inherit;
          box-shadow: rgb(20, 97, 225) 0px 0px 0px 3px;
        }
        .box-svg {
          width: 100%;
          height: 100%;
          border-radius: inherit;
          svg {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0px;
            top: 0px;
            opacity: 0.75;
          }
        }
        .active {
          svg {
            opacity: 1 !important;
          }
        }
      }
    }

    .box-logo {
      margin-left: 6px;
      margin-top: 6px;
      .wraper-logo {
        display: flex;
        flex-wrap: wrap;
        .cover-logo {
          cursor: pointer;
          margin: 0px 6px 12px;
          position: relative;
          user-select: none;
          height: 76px;
          width: 76px;
          border-radius: 4px;
          .wraper-two {
            width: 100%;
            height: 100%;
            border-radius: inherit;
            .layer {
              width: calc(100% + 4px);
              height: calc(100% + 4px);
              position: absolute;
              top: -2px;
              left: -2px;
              border-radius: inherit;
              box-shadow: rgb(20, 97, 225) 0px 0px 0px 3px;
            }
            img {
              width: 100%;
              height: 100%;
              position: absolute;
              top: 0%;
              left: 0%;
              object-fit: contain;
              border-radius: inherit;
              background-color: rgb(228, 230, 235);
              opacity: 1;
            }
          }
        }

        .add-logo {
          width: 76px;
          height: 76px;
          margin: 0px 6px 12px;
          position: relative;
          display: inline-flex;
          -webkit-box-pack: center;
          justify-content: center;
          -webkit-box-align: center;
          align-items: center;
          border-radius: 4px;
          border: 1px solid rgb(189, 191, 199);
          overflow: hidden;
          cursor: pointer;
          input {
            position: absolute;
            width: 0px;
            height: 0px;
            opacity: 0;
          }
          .add-plus {
            font-size: 25px;
            cursor: pointer;
          }
        }
      }
    }
  }
}
